<template>
  <div>

    <el-container style="width:100%;height: 700px; border: 1px solid #eee">
      <el-aside width="650px" style="background-color: #fff;float:left;">

        <el-tree
          :data="data"
          :props="defaultProps"
          show-checkbox
          node-key="id"
          default-expand-all
          :expand-on-click-node="false"
          :render-content="renderContent">
        </el-tree>
      </el-aside>
      <el-main  style="width: 400px">
        <el-form   :model="tenchplanActive" label-width="140px" >
          <el-form-item label="章节/课时名称" >
            <el-input v-model="tenchplanActive.pname" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="课程类型" >
            <el-radio-group v-model="tenchplanActive.ptype">
              <el-radio class="radio" label='1'>视频</el-radio>
              <el-radio class="radio" label='2'>文档</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="学习时长（分钟）" >
            <el-input v-model="tenchplanActive.timelength" auto-complete="off" ></el-input>
          </el-form-item>
          <el-form-item label="媒资文件" >
            {{tenchplanActive.mediaFileOriginalName}}
          </el-form-item>
          <el-form-item label="" >
            <el-button type="primary" v-on:click="querymedia">选择媒资文件</el-button>
          </el-form-item>

          <el-form-item label="排序字段" >
            <el-input v-model="tenchplanActive.orderby" auto-complete="off" ></el-input>
          </el-form-item>
          <el-form-item label="章节/课时介绍" prop="description">
            <el-input type="textarea" v-model="tenchplanActive.description" ></el-input>
          </el-form-item>

          <el-form-item label="状态" >
            <el-radio-group v-model="tenchplanActive.status" >
              <el-radio class="radio" label="0" >未发布</el-radio>
              <el-radio class="radio" label='1'>已发布</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item  >
            <el-button type="primary" v-on:click="update">保存</el-button>
          </el-form-item>

        </el-form>
      </el-main>
    </el-container>
    <el-dialog title="选择媒资文件" :visible.sync="mediaFormVisible">
      <media-list v-bind:ischoose="true" @choosemedia="choosemedia"></media-list>
    </el-dialog>
  </div>
</template>
<script>
  import * as courseApi from '../../api/course';
  import utilApi from '../../../../common/utils';
  import mediaList from '@/module/media/page/media_list.vue';
  let id = 1000;
  export default {
    components:{
      mediaList
    },
    data() {
      return {
        mediaFormVisible:false,
        dotype:'',
        courseid:'',
        tenchplanActive:{},
        data: [{
          id: 1,
          label: '一级 1',
          children: [{
            id: 4,
            label: '二级 1-1',
            children: [{
              id: 9,
              label: '三级 1-1-1'
            }, {
              id: 10,
              label: '三级 1-1-2'
            }]
          }]
        }, {
          id: 2,
          label: '一级 2',
          children: [{
            id: 5,
            label: '二级 2-1'
          }, {
            id: 6,
            label: '二级 2-2'
          }]
        }, {
          id: 3,
          label: '一级 3',
          children: [{
            id: 7,
            label: '二级 3-1'
          }, {
            id: 8,
            label: '二级 3-2'
          }]
        }],
        defaultProps: {
          children: 'children',
          label: 'pname'
        }
      }
    },
    methods: {
      //打开查询媒资文件窗口
      querymedia(){
          this.mediaFormVisible = true;
      },
      choosemedia(mediaId,fileOriginalName,mediaUrl){
          this.tenchplanActive.mediaId = mediaId;
          this.tenchplanActive.mediaFileOriginalName = fileOriginalName;
          this.tenchplanActive.mediaUrl = mediaUrl;
          this.mediaFormVisible = false;
      },
      append(data) {
        let grade = data.grade;
        if(grade>2){
          this.$message.error('只允许向根结点和根据结点下的二级结点添加章节信息！');
        }else{
          let pname_new = '';
          let parentid_new = data.id;
          if(grade == 1){
            pname_new = '我是一个新章节，请点击修改填写正确信息';
          }else{
            pname_new = '我是一个新课时，请点击修改填写正确信息';
          }
          const newTeachplan = {courseid:this.courseid,pname: pname_new,parentid:parentid_new};
          courseApi.addTeachplan(newTeachplan).then((res) => {
            console.log(res);
            data.children.push(res);
          });
        }

      },
      edit(data){
          //alert(data.id);
        courseApi.getTeachplan(data.id).then((res) => {
          console.log(res);
          this.tenchplanActive = res;
        });
      },
      update(){
        if(!this.tenchplanActive || !this.tenchplanActive.id){
          this.$message.error('请选择要修改的章节/课时');
        }else{
            let id = this.tenchplanActive.id
          courseApi.updateTeachplan(id,this.tenchplanActive).then((res) => {
            if(res.success){
              this.$message.success('提交成功');
              //刷新整个树
              this.findTeachplan();
              this.tenchplanActive={};
            }else{
              this.$message.error('提交失败');
            }
          });
        }
      },
      remove(node, data) {
        const parent = node.parent;
        const children = parent.data.children || parent.data;
        const index = children.findIndex(d => d.id === data.id);
        children.splice(index, 1);

      },
      renderContent(h, { node, data, store }) {
        return (
          <span style="flex: 1; display: flex; align-items: center; justify-content: space-between; font-size: 14px; padding-right: 8px;">
            <span>
              <span>{node.label}</span>
            </span>
            <span>
              <el-button style="font-size: 12px;" type="text" on-click={ () => this.append(data) }>添加</el-button>
              <el-button style="font-size: 12px;" type="text" on-click={ () => this.edit(data) }>修改</el-button>
              <el-button style="font-size: 12px;" type="text" on-click={ () => this.remove(node, data) }>删除</el-button>
            </span>
          </span>);
      },
      findTeachplan(){
        courseApi.findTeachplanList(this.courseid).then((res) => {

          if(res){
           this.data = res;
//            console.log(this.data);
          }

        });
      }
    },
    mounted(){
      this.dotype = this.$route.params.dotype;
      //课程id
      this.courseid = this.$route.params.courseid;
      //课程计划
      this.findTeachplan();
    }
  }
</script>
<style scoped>


  .el-main {
    float:right;
    width:500px;
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
    line-height: 160px;
  }
</style>



