
import upload from '@/module/media/page/upload.vue';
import media_list from '@/module/media/page/media_list.vue';
import Home from '@/module/home/page/home.vue';
export default [
  {
    path: '/media',
    component: Home,
    name: '媒资管理',
    hidden: false,
    iconCls: 'el-icon-document',
    children: [
      { path: '/media/upload', name: '上传文件',component: upload,hidden: false
      },
      { path: '/media/list', name: '我的媒资',component: media_list,hidden: false},

    ]
  }
]



