<template >
  <div>
    <course-steps v-bind:active="stepIndex"  ></course-steps>
    <router-view class="main" ></router-view>
  </div>
</template>
<script>
  import courseSteps from '../components/course_steps.vue';
export default {
  components:{
    courseSteps
  },
  data() {
    return {
      stepIndex:0

    }
  },
  methods: {

  },
  created(){
      //根据传入step去选中step
      this.stepIndex= Number(this.$route.params.step)
  },
  mounted(){

  }
}
</script>
<style scoped>


</style>



